<template>
    <div class="conversor">
        <h2> {{moedaA}} to {{moedaB}} </h2>
        <input type="text" v-model="moedaA_value" v-bind:placeholder="moedaA">
        <input type="button" value="Converter" v-on:click="converter">
        <h2>{{moedaB_value}}</h2>
    </div>

</template>

<script>
export default {
    nome:"Conversor",
    props: ["moedaA","moedaB"],
    data() {
        return{
        moedaA_value: "",
        moedaB_value: 0
        };
    },
    methods:{
        converter(){
            let de_para = this.moedaA + "_" + this.moedaB;

            /*==> www.currencyconverterapi.com <==*/
            let url = "https://free.currconv.com/api/v7/convert?q=" +
            de_para + "&compact=ultra&apiKey=a006ff77bd46884dc9e5";

            fetch(url)
            .then(res=>{
                return res.json();
            })
                .then(json=> {
                    let cotacao = json[de_para];
                    this.moedaB_value = (cotacao * parseFloat(this.moedaA_value)).toFixed(
                        2);
                });
        }
    }
};

</script>

<style scoped>
.conversor {
    padding:20px;
    max-width: 300px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0.2);
}

@media (max-width: 560px){
    #app {
    margin: auto;
    max-width: 900px;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align:center;
    color: #2c3e50;
    margin-top: 60px;
}

.conversor {
    margin: auto;
    margin-top: 20px;
    padding:10px;
    max-width: 500px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0.2);

    justify-content: space-around;
}
}
</style>


